
import { defineComponent, ref, computed, watch, onMounted, onUnmounted, nextTick } from 'vue'
import { useStore } from 'vuex'
import { useRoute, onBeforeRouteLeave } from 'vue-router'
import { message, Modal } from 'ant-design-vue'
import { pickBy, mapValues } from 'lodash-es'
import PublishForm from './PublishForm.vue'
import ChannelForm from './ChannelForm.vue'
import HistoryArea from './HistoryArea.vue'
import EditWrapper from '../components/EditWrapper.vue'
import ComponentsList from '../components/ComponentsList.vue'
import ContextMenu from '../components/ContextMenu.vue'
import EditGroup from '../components/EditGroup.vue'
import PropsTable from '../components/PropsTable.vue'
import LayerList from '../components/LayerList.vue'
import UserProfile from '../components/UserProfile.vue'
import InputEdit from '../components/InputEdit.vue'
import mapPropsToComponents from '../propsMap'
import { GlobalDataProps } from '../store/index'
import { ComponentData } from '../store/editor'
import { initHotKeys } from '../plugins/hotKeys'
import showError from '../hooks/useShowError'
import { takeScreenshotAndUpload, UploadImgProps, imageDimensions } from '../helper'
import { baseH5URL } from '../main'

export type TabType = 'component' | 'layer' | 'page'
export default defineComponent({
  name: 'Home',
  components: {
    EditWrapper,
    ComponentsList,
    EditGroup,
    LayerList,
    PropsTable,
    PublishForm,
    ChannelForm,
    ContextMenu,
    UserProfile,
    InputEdit,
    HistoryArea
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const route = useRoute()
    const components = computed(() => store.state.editor.components)
    const currentId = computed(() => store.state.editor.currentElement)
    const currentEditing = computed(() => store.state.editor.currentEditing)
    const currentElement = computed<ComponentData>(() => store.getters.getCurrentElement)
    const userInfo = computed(() => store.state.user)
    const pageState = computed(() => store.state.editor.page)
    const isDirty = computed(() => store.state.editor.isDirty)
    const isChangedNotPublished = computed(() => store.state.editor.isChangedNotPublished)
    const globalStatus = computed(() => store.state.status)
    const visible = ref(false)
    const showModal = ref(false)
    const canvasFix = ref(false)
    const activePanel = ref<TabType>('component')
    // add status for saving and publishing
    const isSaving = ref(false)
    const isPublishing = ref(false)
    initHotKeys()
    showError()
    const currentWorkId = route.params.id
    let timer: any
    const previewURL = computed(() => `${baseH5URL}/p/preview/${pageState.value.id}-${pageState.value.uuid}`)
    const saveWork = (showMessage = false) => {
      isSaving.value = true
      return store.dispatch('saveWorkT', { id: currentWorkId }).then(() => {
        if (showMessage) {
          message.success('保存成功', 2)
        }
      }).finally(() => {
        isSaving.value = false
      })
    }
    const previewWork = () => {
      saveWork(true).then(() => { visible.value = true })
    }
    const takeScreenUpdate = async (checkSave = false) => {
      // remove select condition
      store.commit('setActive', '')
      activePanel.value = 'component'
      // remove box shadow to fix html2canvas issue
      canvasFix.value = true
      await nextTick()
      try {
        const rawData = await takeScreenshotAndUpload('canvas-area')
        if (rawData) {
          store.commit('updatePage', { key: 'coverImg', value: rawData.data.urls[0] })
        }
      } catch (e) {
        console.error(e)
      } finally {
        canvasFix.value = false
        if (checkSave) {
          saveWork()
        }
      }
    }
    const publishWork = async () => {
      isPublishing.value = true
      try {
        await takeScreenUpdate()
      } catch (e) {
        console.error(e)
      } finally {
        await store.dispatch('saveAndPublishWork', { id: currentWorkId })
        visible.value = false
        showModal.value = true
        isPublishing.value = false
      }
    }

    onMounted(() => {
      // clear the editor store data
      store.commit('resetEditor')
      // fetch work
      if (currentWorkId) {
        store.dispatch('getWork', currentWorkId)
      }
      // start autoSave timer, per 30 secs
      timer = setInterval(() => {
        if (isDirty.value) {
          saveWork()
        }
      }, 1000 * 30)
    })
    onUnmounted(() => {
      clearInterval(timer)
    })
    onBeforeRouteLeave((to, from, next) => {
      // 离开之前截图并保存
      // 未保存，截图保存
      if (isDirty.value) {
        Modal.confirm({
          title: '作品还未保存，是否保存？',
          okText: '保存',
          okType: 'primary',
          cancelText: '不保存',
          onOk: () => {
            takeScreenUpdate(true).then(() => { next() })
          },
          onCancel () {
            next()
          }
        })
      // 有修改但是未发布 截图保存
      } else if (isChangedNotPublished.value) {
        takeScreenUpdate(true).then(() => { next() })
      } else {
        next()
      }
    })
    watch(activePanel, (newValue) => {
      if (newValue !== 'component') {
        store.commit('setActive', '')
      }
    })
    const onItemCreated = (component: ComponentData) => {
      // we should copy this props, not pass by ref
      store.commit('addComponentToEditor', { name: component.name, props: { ...component.props } })
    }
    const setActive = (id: string, notSwitchPanel = false) => {
      store.commit('setActive', id)
      if (!notSwitchPanel) {
        activePanel.value = 'component'
      }
    }
    const setEditing = (id: string) => {
      store.commit('setEditing', id)
      activePanel.value = 'component'
      nextTick(() => {
        const ele = document.querySelector('#item-text textarea') as HTMLTextAreaElement
        if (ele) {
          ele.focus()
        }
      })
    }
    const handleChange = (data: any) => {
      store.commit('updateComponent', data)
    }
    const setPageSetting = (e: Event) => {
      const currentTarget = e.target as HTMLElement
      if (currentTarget.classList.contains('body-container')) {
        store.commit('setActive', '')
        activePanel.value = 'page'
      }
    }
    // clear component or page selection when clicking the gray area
    const clearSelection = (e: Event) => {
      const currentTarget = e.target as HTMLElement
      if (currentTarget.classList.contains('preview-container')) {
        store.commit('setActive', '')
        activePanel.value = 'page'
      }
    }
    const updatePosition = (data: { left: number; top: number; id: string; width: number; height: number}) => {
      const { id } = data
      const newPair = mapValues(pickBy(data, (v, k) => k !== 'id'), (value) => {
        return value + 'px'
      })
      const keysArr = Object.keys(newPair)
      const valuesArr = Object.values(newPair)
      store.commit('updateComponent', { key: keysArr, value: valuesArr, id, isProps: true })
    }
    const titleChange = (newTitle: string) => {
      store.commit('updatePage', { key: 'title', value: newTitle })
      nextTick(() => {
        saveWork()
      })
    }
    const adjustHeightOnUpload = (event: { data: UploadImgProps; key: string }) => {
      // check the key is background and data is correct
      if (event.key === 'backgroundImage') {
        imageDimensions(event.data.file).then(dimension => {
          const maxWidth = 375
          const rate = dimension.height / dimension.width
          if (rate > 1) {
            store.commit('updatePage', { key: 'height', value: rate * maxWidth + 'px', level: 'props' })
          }
        })
      }
    }
    return {
      visible,
      showModal,
      onItemCreated,
      handleChange,
      components,
      setActive,
      setEditing,
      currentId,
      currentElement,
      currentEditing,
      mapPropsToComponents,
      updatePosition,
      setPageSetting,
      activePanel,
      pageState,
      userInfo,
      globalStatus,
      saveWork,
      previewWork,
      publishWork,
      titleChange,
      clearSelection,
      previewURL,
      adjustHeightOnUpload,
      canvasFix,
      isSaving,
      isPublishing
    }
  }
})
