
import { defineComponent } from 'vue'
// import { FileImageOutlined, LoadingOutlined, FieldStringOutlined, BuildOutlined } from '@ant-design/icons-vue'
import { message } from 'ant-design-vue'
// import Uploader from './Uploader.vue'
import { componentsDefaultProps } from '../defaultProps'
import { commonUploadCheck, imageDimensions, UploadImgProps } from '../helper'
const textDefaultProps = componentsDefaultProps['p-text'].props
const imageDefaultProps = componentsDefaultProps['p-image'].props
const shapeDefaultProps = componentsDefaultProps['p-shape'].props
interface CreateComponentType {
  name: string;
  text?: string;
  type?: string;
  props: { [key: string]: string };
}
const generateResetCss = (name: string) => {
  return {
    position: 'static',
    ...((name !== 'p-shape') && { height: '' })
  }
}
// the component name list
const textPropsList = [
  {
    text: '大标题',
    fontSize: '30px',
    fontWeight: 'bold',
    tag: 'h2'
  },
  {
    text: '楷体副标题',
    fontSize: '20px',
    fontWeight: 'bold',
    fontFamily: '"KaiTi","STKaiti"',
    tag: 'h2'
  },
  {
    text: '正文内容',
    tag: 'p'
  },
  {
    text: '宋体正文内容',
    tag: 'p',
    fontFamily: '"SimSun","STSong"'
  },
  {
    text: 'Arial style',
    tag: 'p',
    fontFamily: '"Arial", sans-serif'
  },
  {
    text: 'Comic Sans',
    tag: 'p',
    fontFamily: '"Comic Sans MS"'
  },
  {
    text: 'Courier New',
    tag: 'p',
    fontFamily: '"Courier New", monospace'
  },
  {
    text: 'Times New Roman',
    tag: 'p',
    fontFamily: '"Times New Roman", serif'
  },
  {
    text: '链接内容',
    color: '#1890ff',
    textDecoration: 'underline',
    tag: 'p'
  },
  {
    text: '按钮内容',
    color: '#ffffff',
    backgroundColor: '#1890ff',
    borderWidth: '1px',
    borderColor: '#1890ff',
    borderStyle: 'solid',
    borderRadius: '2px',
    paddingLeft: '10px',
    paddingRight: '10px',
    paddingTop: '5px',
    paddingBottom: '5px',
    width: '100px',
    tag: 'button',
    textAlign: 'center'
  }
]
const textList: CreateComponentType[] = textPropsList.map((prop) => {
  return {
    name: 'p-text',
    props: {
      ...textDefaultProps,
      ...(prop as any)
    }
  }
})
const imgSourceList = [
  'http://static.imooc-lego.com/upload-files/logo-white-735536.png',
  'http://static.imooc-lego.com/upload-files/logo-black-049885.png',
  'http://static.imooc-lego.com/upload-files/528w-0ilmEQMomZ8-108048.png',
  'http://static.imooc-lego.com/upload-files/frame-096161.png',
  'http://static.imooc-lego.com/upload-files/text-449964.png',
  'http://static.imooc-lego.com/upload-files/text2-288504.png',
  'http://static.imooc-lego.com/upload-files/money-664239.png',
  'http://static.imooc-lego.com/upload-files/bag-904186.png',
  'http://static.imooc-lego.com/upload-files/text3-086652.png',
  'http://static.imooc-lego.com/upload-files/text4-145592.png'
]
const imageList: CreateComponentType[] = imgSourceList.map((url) => {
  return {
    name: 'p-image',
    props: {
      ...imageDefaultProps,
      imageSrc: url,
      width: '150px'
    }
  }
})
const shapePropsList = [
  {
    backgroundColor: '#efefef',
    borderWidth: '1px',
    borderStyle: 'solid',
    borderColor: '#ccc',
    width: '100px',
    height: '50px'
  },
  {
    backgroundColor: '#efefef',
    borderWidth: '1px',
    borderStyle: 'solid',
    borderColor: '#ccc',
    borderRadius: '100px',
    width: '100px',
    height: '100px'
  },
  {
    backgroundColor: '#efefef',
    borderWidth: '1px',
    borderStyle: 'solid',
    borderColor: '#ccc',
    width: '100px',
    height: '100px'
  },
  {
    backgroundColor: '#36cfc9',
    width: '100px',
    height: '50px'
  },
  {
    backgroundColor: '#40a9ff',
    borderRadius: '100px',
    width: '100px',
    height: '100px'
  },
  {
    backgroundColor: '#9254de',
    borderWidth: '5px',
    borderStyle: 'solid',
    borderColor: '#ccc',
    width: '100px',
    height: '100px'
  }
]
const shapeList: CreateComponentType[] = shapePropsList.map((prop) => {
  return {
    name: 'p-shape',
    props: {
      ...shapeDefaultProps,
      ...(prop as any)
    }
  }
})

export default defineComponent({
  components: {
    // Uploader,
    // FileImageOutlined,
    // LoadingOutlined,
    // FieldStringOutlined,
    // BuildOutlined
  },
  name: 'components-list',
  emits: ['on-item-click'],
  setup (props, context) {
    const onItemClick = (data: CreateComponentType) => {
      if (data.type !== 'upload') {
        context.emit('on-item-click', data)
      }
    }
    const handleFileUploaded = (uploadedData: UploadImgProps) => {
      const data = {
        name: 'p-image',
        props: {
          ...imageDefaultProps
        }
      } as CreateComponentType
      message.success('上传成功')
      data.props.imageSrc = uploadedData.data.urls[0]
      imageDimensions(uploadedData.file).then(dimension => {
        const maxWidth = 300
        data.props.width = ((dimension.width > maxWidth) ? maxWidth : dimension.width) + 'px'
        context.emit('on-item-click', data)
      })
    }
    return {
      textList,
      imageList,
      shapeList,
      onItemClick,
      commonUploadCheck,
      handleFileUploaded,
      generateResetCss
    }
  }
})
